/* Base utilities shared across FA/EN builds.
   Keep these selectors as the single source of truth
   so the design system does not override them. */

body {
  margin: 0;
  font-family: var(--font-body, sans-serif);
}

/* --- Toast --- */

.dynamic-toast {
  position: fixed;
  bottom: 1rem;
  left: 50%;
  transform: translateX(-50%) translateY(100%);
  opacity: 0;
  background: var(--toast-bg, rgba(0, 0, 0, 0.8));
  color: var(--toast-color, #fff);
  padding: 0.5rem 1rem;
  border-radius: 4px;
  z-index: 1000;
  font-size: 0.875rem;
  line-height: 1.3;
  max-width: min(90vw, 420px);
  text-align: center;
  box-shadow: 0 8px 24px var(--shadow-color-light, rgba(0, 0, 0, 0.3));
  transition:
    transform var(--transition-duration-medium, 200ms)
      var(--transition-easing-standard, ease),
    opacity var(--transition-duration-medium, 200ms)
      var(--transition-easing-standard, ease);
}

.dynamic-toast.show {
  transform: translateX(-50%) translateY(0);
  opacity: 1;
}

/* --- Themes --- */

.dark-mode {
  background: #0f172a;
  color: #f8fafc;
  --primary-color: #38bdf8;
  --primary-rgb: 56, 189, 248;
  --secondary-color: #f1f5f9;
  --accent-color: #60a5fa;
  --cta-text-color: #0f172a;
  --highlight-color: #fbbf24;
  --highlight-rgb: 251, 191, 36;
  --highlight-darker: #d97706;
  --background-start-color: #0f172a;
  --background-end-color: #1e293b;
  --container-bg: #1e293b;
  --card-bg: #1e293b;
  --navbar-bg: rgba(15, 23, 42, 0.95);
  --giscus-bg: #1e293b;
  --skills-bg: #334155;
  --toast-bg: var(--primary-color);
  --toast-color: #0f172a;
  --shadow-color-light: rgba(0, 0, 0, 0.3);
  --shadow-color-medium: rgba(0, 0, 0, 0.5);
  --shadow-color-strong: rgba(0, 0, 0, 0.7);
  --text-color: #e2e8f0;
  --text-color-light: #cbd5e1;
  --text-color-link: var(--primary-color);
  --heading-color: var(--secondary-color);
  --tagline-color: #94a3b8;
  --footer-color: #94a3b8;
  --lang-switcher-active-color: #f8fafc;
  --border-color: #334155;
  --timeline-line-color: #475569;
  --timeline-dot-border-color: var(--primary-color);
  --timeline-content-border-color: #334155;
  --giscus-border: 1px solid var(--border-color);
  --faq-link-hover-color: var(--primary-color);
  --card-link-hover-text-color: #0f172a;
}

.light-mode {
  background: #ffffff;
  color: #0f172a;
}

/* Footer links */

.footer-links {
  margin-top: 1rem;
  text-align: center;
}

.footer-link {
  color: var(--footer-color, #bbb);
  font-size: 0.9rem;
  text-decoration: none;
  padding: var(--space-1, 0.25rem) var(--space-2, 0.5rem);
  border-radius: 6px;
  transition:
    color var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease),
    background-color var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease);
  will-change: color, background-color;
}

.footer-link:hover,
.footer-link:focus-visible {
  background-color: rgba(100, 100, 100, 0.1);
  color: var(--faq-link-hover-color, #2563eb);
  outline: none;
}

/* Micro profile */

.micro-profile {
  font-size: 0.75rem;
  color: var(--text-color-light, #555);
  opacity: 0.6;
  text-align: center;
  max-width: 400px;
  margin: 2rem auto 0;
  padding: 0.5rem 1rem;
  border-top: 1px dashed var(--border-color, #ccc);
  will-change: opacity;
}

.micro-profile img.u-photo {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 50%;
  margin: 0.5rem auto;
  display: block;
}

.micro-profile a.u-url,
.micro-profile a.u-email {
  display: inline-block;
  color: var(--text-color-link, #0066cc);
  font-weight: bold;
  text-decoration: none;
}

.micro-profile a.u-url:hover,
.micro-profile a.u-email:hover {
  text-decoration: underline;
}

/* Theme switch */

.theme-switch {
  position: relative;
  display: inline-flex;
  align-items: center;
  width: 48px;
  height: 24px;
  margin: 0 var(--space-md, 1rem);
  vertical-align: middle;
}

.theme-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  inset: 0;
  background-color: var(--text-color-light, #9ca3af);
  transition: background-color var(--transition-duration-short, 120ms)
    var(--transition-easing-standard, ease);
  border-radius: 24px;
}

.slider::before {
  position: absolute;
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: var(--card-bg, #ffffff);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;

  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  content: "\f185";
  color: var(--primary-color, #0f172a);
  font-size: 0.8em;
  transition:
    transform var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease),
    background-color var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease),
    color var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease);
}

input:checked + .slider {
  background-color: var(--primary-color, #2563eb);
}

input:focus-visible + .slider {
  box-shadow: 0 0 0 3px var(--accent-color, #2563eb),
    0 0 0 6px var(--shadow-color-light, rgba(0, 0, 0, 0.18));
}

input:checked + .slider::before {
  transform: translateX(24px);
  content: "\f186";
  color: #ffffff;
}

/* Utility visibility */

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* Utility layout */

.min-h-screen {
  min-height: 100vh;
}

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.items-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.text-center {
  text-align: center;
}

.p-8 {
  padding: 2rem;
}

.text-lg {
  font-size: 1.125rem;
}

.text-4xl {
  font-size: 2.25rem;
}

.font-bold {
  font-weight: 700;
}

.mb-4 {
  margin-bottom: 1rem;
}

.mb-6 {
  margin-bottom: 1.5rem;
}

/* Global focus states */

a:focus-visible,
button:focus-visible,
input:focus-visible,
select:focus-visible,
textarea:focus-visible {
  outline: 3px solid var(--accent-color, #2563eb);
  outline-offset: var(--space-xs, 0.15rem);
  box-shadow: 0 0 0 4px var(--shadow-color-light, rgba(0, 0, 0, 0.15));
  z-index: 10;
  position: relative;
}

a:focus:not(:focus-visible),
button:focus:not(:focus-visible),
input:focus:not(:focus-visible),
select:focus:not(:focus-visible),
textarea:focus:not(:focus-visible) {
  outline: none;
}

/* Press card styles */

.press-card {
  display: flex;
  align-items: center;
  gap: var(--space-md, 1rem);
  padding: var(--space-md, 1rem);
  margin-bottom: var(--space-md, 1rem);
  background-color: var(--card-bg, #ffffff);
  border: 1px solid var(--border-color, #e5e7eb);
  border-radius: var(--border-radius-lg, 1rem);
  transition:
    box-shadow var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease),
    transform var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease);
}

.press-card:hover {
  box-shadow: 0 4px 12px var(--shadow-color-medium, rgba(0, 0, 0, 0.16));
  transform: translateY(-1px);
}

.press-card img {
  width: 48px;
  height: 48px;
  flex-shrink: 0;
}

/* Download button transitions */

.download-button {
  transition:
    background-color var(--transition-duration-short, 120ms)
      var(--transition-easing-standard, ease),
    transform var(--transition-duration-short, 120ms)
      var(--transition-easing-springy, cubic-bezier(0.18, 0.89, 0.32, 1.28));
}

.download-button:hover,
.download-button:focus-visible {
  background-color: var(--primary-color, #2563eb);
  transform: translateY(-2px);
  text-decoration: none;
  color: #fff;
}

/* Offline info */

.offline-info {
  display: none;
  padding: 0.5rem 1rem;
  background-color: #fffbea;
  color: #854d0e;
  border: 1px solid #fadcb3;
  border-radius: 10px;
  margin-top: 0.5rem;
  text-align: center;
  font-size: 0.875rem;
}

body.offline .offline-info {
  display: block;
}

/* --- Disable AOS hiding when animations are off --- */

.aos-disabled [data-aos] {
  opacity: 1 !important;
  transform: none !important;
  transition: none !important;
}

/* --- Modern Enhancements --- */

html {
  scroll-behavior: smooth;
}

/* Hero Section Enhancements */
.hero {
  position: relative;
  overflow: hidden;
}

.dark-mode .hero::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle at center, rgba(97, 218, 251, 0.1) 0%, transparent 50%);
  z-index: -1;
  animation: pulse-glow 10s infinite alternate;
}

@keyframes pulse-glow {
  0% { transform: scale(1); opacity: 0.5; }
  100% { transform: scale(1.2); opacity: 0.8; }
}

.hero .profile-image {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 4px solid var(--primary-color);
}

.hero .profile-image:hover {
  transform: scale(1.05) rotate(2deg);
  box-shadow: 0 0 20px var(--primary-color);
}

/* Button Enhancements */
.hero-btn {
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: all 0.3s ease;
}

.hero-btn::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  transition: width 0.3s ease;
  z-index: -1;
}

.hero-btn:hover::after {
  width: 100%;
}

.hero-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

/* Timeline Enhancements */
.timeline-content {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.timeline-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px var(--shadow-color-medium);
  border-color: var(--primary-color);
}

/* Scroll Progress Bar */
.sohrabi-progress {
  position: fixed;
  top: 0;
  left: 0;
  height: 4px;
  background: var(--primary-color);
  width: 0%;
  z-index: 9999;
  transition: width 0.1s linear;
}

/* Connect Section Grid Layout */
.connect-links-block ul {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 1rem;
  list-style: none;
  padding: 0;
}

.connect-links-block ul li {
  margin: 0; /* Reset default margin */
}

.connect-links-block ul li a {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-decoration: none;
  color: var(--text-color);
  height: 100%;
  font-size: 0.9rem;
}

.connect-links-block ul li a:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 20px var(--shadow-color-light);
  border-color: var(--primary-color);
  color: var(--primary-color);
  background: var(--background-start-color);
}

.connect-links-block ul li a i {
  font-size: 1.2rem;
  width: 24px;
  text-align: center;
}

/* Back to Top Button */
.back-to-top {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background-color: var(--primary-color);
  color: #fff;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  box-shadow: 0 4px 12px var(--shadow-color-medium);
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px);
  transition: all 0.3s ease;
  z-index: 999;
}

.back-to-top.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.back-to-top:hover {
  background-color: var(--accent-color);
  transform: translateY(-5px);
  box-shadow: 0 6px 16px var(--shadow-color-strong);
}

/* RTL Support for Back to Top */
[dir="rtl"] .back-to-top {
  right: auto;
  left: 2rem;
}

/* Copy Button */
.contact-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.copy-btn {
  background: none;
  border: none;
  color: var(--text-color-light);
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 50%;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.copy-btn:hover {
  background-color: var(--secondary-color);
  color: var(--primary-color);
}

.copy-btn:active {
  transform: scale(0.95);
}

/* Print Styles */
@media print {
  .navbar,
  .back-to-top,
  .theme-switch,
  .lang-switcher,
  .hero-cta-group,
  .copy-btn,
  #theme-toast,
  #scroll-progress-bar {
    display: none !important;
  }

  body {
    background: #fff !important;
    color: #000 !important;
    padding-top: 0;
  }

  a {
    text-decoration: none;
    color: #000;
  }

  /* Show URL after links */
  a[href^="http"]::after {
    content: " (" attr(href) ")";
    font-size: 0.8em;
    color: #666;
  }

  /* Don't show URL for internal links or specific classes if needed */
  a[href^="#"]::after,
  .no-print-url::after {
    content: "";
  }

  .card, .project-card {
    break-inside: avoid;
    border: 1px solid #ddd;
    box-shadow: none;
  }

  section {
    break-inside: avoid;
  }
}
